import React, { useState } from "react";
import { useHistory } from "react-router";
import { Header } from "../../sub_components";
import "./index.css";
import logo from "../../../media/pictures/logo.svg";

function ComingSoon() {
	const [time, setTime] = useState("");

	// Set the date we're counting down to
	var countDownDate = new Date("Oct 09, 2021 01:00:00").getTime();
	// Update the count down every 1 second
	var x = setInterval(function () {
		// Get today's date and time
		var now = new Date().getTime();

		// Find the distance between now and the count down date
		var distance = countDownDate - now;

		// Time calculations for days, hours, minutes and seconds
		var days = Math.floor(distance / (1000 * 60 * 60 * 24));
		var hours = Math.floor(
			(distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
		);
		var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
		var seconds = Math.floor((distance % (1000 * 60)) / 1000);

		// Display the result in the element with id="demo"
		setTime(days + "d " + hours + "h " + minutes + "m " + seconds + "s ");

		// If the count down is finished, write some text
		if (distance < 0) {
			clearInterval(x);
			setTime("EXPIRED");
		}
	}, 1000);

	const history = useHistory();

	return (
		<div className="comingSoon">
			<img src={logo} alt="" className="comingSoon__logo" />
			<label className="comingSoon__label">
				Coming Soon
				<label
					className="rainbowWrap"
					onClick={(e) => {
						history.push("/home");
					}}
				>
					{time}
				</label>
			</label>

			<br />
			<br />
			<br />
		</div>
	);
}

export default ComingSoon;
