import React from "react";
import "./index.css";
import logo from "../../../media/pictures/logo-name-bgtp.svg";

function Footer() {
	return (
		<div className="footer">
			<div className="footer__top">
				<form
					onSubmit={(e) => e.preventDefault()}
					className="footer__joinEmailList"
				>
					<p>Get tips, updates, promotions and more.</p>
					<input
						type="email"
						name="emailList-email"
						id="emailList-email"
						placeholder="Email:"
					/>
					<button type="submit">Submit</button>
				</form>
			</div>
			<div className="footer__bottom">
				<div>
					<div className="footer__info">
						<img src={logo} alt="" className="footer__logo" />
						<div className="footer__name">
							<h2>ILand Motors</h2>
							<p>Recreational Vehicle Manufaturer</p>
						</div>
						<br />
						<p className="footer__address">
							<h3>Address</h3>
							<p>Claremont District</p>
							<p>Jericho P.O.</p> <p>Hanover</p> <p>Jamaica</p>
						</p>
						<br />
						<p className="footer__contact">
							<h3>Contact Us</h3>
							<p>Tele: +1 (876) 772-1282</p>
							<p>Email: ilandmotorsja@gmail.com</p>
						</p>
						<br />
					</div>
					<div className="footer__links"></div>
					<div>
						<div className="footer__hours">
							<h3>Opening Hours</h3>
							<p>Sundays: 10:00am to 4:00pm</p>
							<p>Mondays - Thursdays: 9:00am to 5:00pm</p>
							<p>Fridays: 9:00am to 4:00pm</p>
						</div>
                        <br />
						<div className="footer__social">
							<h3>Social Links</h3>
							<p>
								Instagram:{" "}
								<a
									href="http://instagram.com/ilandmotors"
									rel="noreferrer"
									target="_blank"
								>
									@ilandmotors
								</a>{" "}
							</p>
							<p>
								Facebook:{" "}
								<a
									href="http://facebook.com/ilandmotors"
									rel="noreferrer"
									target="_blank"
								>
									@ilandmotors
								</a>{" "}
							</p>
							<p>
								Twitter:{" "}
								<a
									href="http://twitter.com/ilandmotors"
									rel="noreferrer"
									target="_blank"
								>
									@ilandmotors
								</a>{" "}
							</p>
						</div>
					</div>
				</div>

				{/* <div>
					Font generated by{" "}
					<a href="https://www.flaticon.com">flaticon.com</a>. Under
					License:{" "}
					<a href="https://file000.flaticon.com/downloads/license/license.pdf">
						Flaticon Basic License
					</a>
					.
					<div>
						By: <a href="http://www.freepik.com">Freepik</a>.
					</div>
				</div> */}
				<p>ILand Motors 2021. All Rights Reserved</p>
			</div>
		</div>
	);
}

export default Footer;
